  background: #fff;
  color: #000;
}
.signin-up-wrp .nav-pills {
  margin-bottom: 25px;
}
.signin-up-wrp .nav-pills .nav-link {
  opacity: 0.3;
  font-size: 2.4rem;
  font-weight: bold;
  letter-spacing: 0.25px;
  color: #000;
  line-height: 1.4;
  position: relative;
}
.signin-up-wrp .nav-pills .nav-link.active:after {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 20px;
  height: 4px;
  border-radius: 2px;
  background-color: #000;
  /* color: #000 !important; */
  top: calc(100% + 10px);
  content: '';
}
.signin-up-wrp .nav-pills .nav-link.active {
  opacity: 1;
}
.overlay-page.active-flex {
  display: flex;
}
@media only screen and (min-width: 768px) {
