/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
html,
body {
  line-height: 1;
  font-family: 'Poppins', sans-serif;
  font-weight: normal;
}
html.over-hidden,
body.over-hidden {
  overflow: hidden;
  height: 100%;
  width: 100%;
}
/*
 1.) Global Typography
 ----------------------------------------*/
*,
:before,
:after {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  outline: 0;
}
/*
 2.0.1) Font Face
 -----------------------------*/
/*Put Your font face Styles here*/

/*
 2.0.2) Global Headings
 -----------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  color: #21263f;
}
h1 {
  /* font-size: 48px; */
  font-size: 2.8rem;
  line-height: 0.875;
  margin: 0 0 2rem 0;
  color: 272b32;
  line-height: normal;
  font-weight: bold;
}
h2 {
  /* font-size: 32px; */
  font-size: 2.2rem;
  line-height: 1.2;
  margin: 0;
  font-weight: 300;
  letter-spacing: 0.31px;
  color: #000000;
  line-height: 1.4;
}
h3 {
  font-size: 2.4rem;
  line-height: 1.2;
  margin: 0 0 30px;
  font-weight: bold;
  color: #f37e00;
}
h4 {
  /* font-size: 21px; */
  font-size: 1.8rem;
  line-height: 1.4;
  margin: 0;
  font-weight: bold;
  color: #000000;
  letter-spacing: 0.15px;
}
h5 {
  /* font-size: 18px; */
  font-size: 2.4rem;
  line-height: 1.49;
  margin: 0 0 25px 0;
  font-weight: 400;
  letter-spacing: 0.12rem;
}
h6 {
  /* font-size: 14px; */
  font-size: 1.4rem;
  line-height: 1.5;
  margin: 0 0 1.5rem 0;
}
/*
 2.0.5) General Text Formatting
 -----------------------------------*/
p {
  font-size: 1.2rem;
  line-height: 1.25;
  letter-spacing: 0.1px;
  color: #000000;
  opacity: 0.5;
  line-height: 1.4;
}
img {
  max-width: 100%;
  height: auto;
}
/*
 2.0.4) List Styles
 ---------------------------------*/

/*
 2.0.5) General Text Formatting
 -----------------------------------*/
/*
 2.0.6) Link style
 -------------------------*/
em,
span {
  display: inline-block;
  font-size: inherit;
}
strong {
  display: inline-block;
  font-size: inherit;
  font-weight: bold;
}
a {
  color: #000;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  display: inline-block;
}
a:hover,
a:focus {
  text-decoration: none;
}
a:active {
  outline: none;
}
/*
 2.0.9) Sprite Icons
 -------------------------*/
[class^='icon-'],
[class*='icon-'] {
  background-position: 0 0;
  background-repeat: no-repeat;
  display: inline-block;
  height: 14px;
  line-height: 14px;
  margin-top: 1px;
  vertical-align: text-top;
  width: 14px;
}

/* Clear Floated Elements
 ----------------------------------------------------------------------------------------------------*/

/* http://sonspring.com/journal/clearing-floats */

.clear {
  clear: both;
  display: block;
  overflow: hidden;
  visibility: hidden;
  width: 0;
  height: 0;
}

/* http://perishablepress.com/press/2008/02/05/lessons-learned-concerning-the-clearfix-css-hack */

.clearfix:after {
  clear: both;
  content: ' ';
  display: block;
  font-size: 0;
  line-height: 0;
  visibility: hidden;
  width: 0;
  height: 0;
}

.clearfix {
  display: inline-block;
}

* html .clearfix {
  height: 1%;
}

.clearfix {
  display: block;
}
