@keyframes pulses {
  0% {
    transform: scale(0.9);
  }
  to {
    transform: scale(1.1);
  }
}
.play-icn:hover img {
  animation: pulses 1s linear 0s infinite alternate;
}
.slick-track {
  display: flex;
}
.slick-arrow {
  width: 40px;
  height: 40px;
  background: rgba(0, 0, 0, 0.5);
  transform: translate(0, 0);
  border-radius: 50%;
  z-index: 1;
}
.slick-arrow.slick-next {
  top: -65px;
  right: 0;
}
.slick-arrow.slick-prev {
  top: -65px;
  right: 60px;
  left: auto;
}
.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  opacity: 1;
  background: rgba(0, 0, 0, 1);
}
.slick-prev::before,
.slick-next::before {
  background: url(/static/media/angle-down-white.172220f7.svg) no-repeat center center;
  background-size: 24px 32px;
  font-size: 0;
  width: 16px;
  height: 10px;
  display: inline-block;
  transform: rotate(90deg);
}
.slick-next::before {
  transform: rotate(-90deg);
}
.overlay-page {
  position: fixed;
  z-index: 10;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  background: rgba(0, 0, 0, 0.8);
  display: none;
}
.overlay-page.active {
  display: block;
}
